import { Model } from "./base/Model";
export class CardItem extends Model {
}
export class AppData extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            items: [],
            email: '',
            phone: '',
            payment: '',
            address: '',
            total: 0
        };
        this.formErrors = {};
    }
    clearBasket() {
        this.basket.forEach(id => {
            this.removeCard(id);
        });
    }
    clearOrder() {
        this.order = {
            items: [],
            total: 0,
            address: '',
            email: '',
            phone: '',
            payment: ''
        };
    }
    removeCard(item) {
        this.basket = this.basket.filter(val => val.id != item.id);
        this.emitChanges('basket:changed');
        this.basket.forEach(card => {
            this.order.items.push(card.id);
            this.order.items = [card.id];
        });
    }
    getTotal() {
        return this.basket.reduce((a, c) => a + c.price, 0);
    }
    setTotal(value) {
        return this.order.total = value;
    }
    setBasket(item) {
        this.basket.push(item);
        this.order.items.push(item.id);
        this.emitChanges('basket:changed');
    }
    isInBasket(item) {
        return this.basket.includes(item);
    }
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    setCatalog(items) {
        this.catalog = items.map(item => new CardItem(item, this.events));
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    setOrderField(field, value) {
        this.order[field] = value;
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.order);
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this.order.phone) {
            errors.phone = 'Необходимо указать телефон';
        }
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        if (!this.order.payment) {
            errors.payment = 'Необходимо указать способ оплаты';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
}
